import React, { Component } from 'react'
import './index.css'

import fetchJsonp from 'fetch-jsonp'
class DetailCont extends Component {
  constructor (props) {
    super(props)
    this.state = {
      data: {}
    }
    this.clickItem = this.clickItem.bind(this)
    this.getData = this.getData.bind(this)
    this.handleData = this.handleData.bind(this)
    this.handleErr = this.handleErr.bind(this)
  }
  getData (url, fn, errfn) {
    var _self = this
    fetchJsonp(url)
      .then(function (response) {
        return response.json()
      }).then(function (json) {
        console.log('json')
        _self.handleData(json)
      }).catch(function (ex) {
        _self.handleErr(ex)
      })
  }

  clickItem (type) {
    this.props.onClick(type)
  }
  clickBackBtn () {
    this.props.onClick()
  }
  handleData (json) {
    console.log(json)
    this.setState({
      data: json
    })
  }
  handleErr (ex) {
    console.log('----err----', ex)
  }
  componentDidMount () {
    this.getData(this.props.url)
  }
  render () {
    return (
      <div className='DetailCont'>
        <div className='topmenu' onClick={this.clickBackBtn.bind(this)}>
          <div className='backbtn'>&lt;返回</div>
          <span>{this.state.data.title}</span>
        </div>
        <div className='contbody'>
          <div className='upbody'>
            <img src={this.state.data.image} alt='图片' />
            <div className='contright'>
              <div className='name'>名称：{this.state.data.title}</div>
              <div className='author'>作者：{(this.state.data.author && this.state.data.author.join(',')) || ''}</div>
              <div className='pubplace'>出版社：{this.state.data.publisher}</div>
              <div className='pubplace'>日期：{this.state.data.pubdate}</div>
              <div className='pubplace'>评分：{this.state.data.rating && this.state.data.rating.average}</div>
              <div className='pubplace'>价钱：{'￥' + parseFloat(this.state.data.price) + '元'}</div>
              <ul className='tagsul'>
                {
                this.state.data.tags && this.state.data.tags.map((item, index) => {
                  if (index <= 2) {
                    return <li className='tag' key={index}>{item.title}</li>
                  }
                })
            }
              </ul>
            </div>
          </div>
          <div className='des'>
            {this.state.data.catalog && <div className='title'>序言</div>}
            <p>{this.state.data.catalog}</p>
            {this.state.data.summary && <div className='title'>简介</div>}
            <p>{this.state.data.summary}</p>
          </div>
        </div>
      </div>
    )
  }
}

module.exports = DetailCont



