import React, { Component } from 'react'
import './index.css'

class Item extends Component {
  handleClick (type) {
    this.props.onClick(type)
  }
  render () {
    let isSelected = this.props.isSelected
    let selectedCls = isSelected ? ' item-selected' : ''
    return (
      <li
        className={'item' + selectedCls}
        onClick={this.handleClick.bind(this, this.props.type)}
            >
        <div className='item-cont'><div className={'icons ' + this.props.type} />{this.props.item}</div>
      </li>
    )
  }
}

export default Item



