import React, { Component } from 'react'
import './style.css'
import Tab from './tab'
import TabCont from './tabcont'
import DetailCont from './detailCont'

class App extends Component {
  constructor () {
    super()
    this.initActiveType = 'book'
    this.state = {
      activedType: this.initActiveType,
      showDetail: false
    }
    this.clickItem = this.clickItem.bind(this)
    this.showDetail = this.showDetail.bind(this)
    this.hideDetail = this.hideDetail.bind(this)
  }
  clickItem (type) {
    this.setState({
      activedType: type
    })
  }
  showDetail (url) {
    this.detailurl = url
    this.setState({
      showDetail: true
    })
  }
  hideDetail () {
    this.setState({
      showDetail: false
    })
  }
  render () {
    const items = [{
      font: '图书',
      type: 'book'
    }, {
      font: '电影',
      type: 'movie'
    }, {
      font: '音乐',
      type: 'music'
    }]
    return (
      <div className='app'>
        {this.state.showDetail && <DetailCont url={this.detailurl} onClick={this.hideDetail} />}
        {<TabCont className={!this.state.showDetail ? 'show' : 'hide'} onClick={this.showDetail} items={items} activedType={this.state.activedType} />}
        {<Tab className={!this.state.showDetail ? 'show' : 'hide'} onClick={this.clickItem} activedType={this.initActiveType} items={items} />}

      </div>
    )
  }
}

module.exports = App



