import React, { Component } from 'react'
import './index.css'
import Item from '../tabItem'

class Tab extends Component {
  constructor (props) {
    super(props)
    this.state = {
      activedType: props.activedType
    }
    this.clickItem = this.clickItem.bind(this)
  }
  clickItem (type) {
    this.setState({
      activedType: type
    })
    this.props.onClick(type)
  }
  render () {
    const activedType = this.state.activedType
    return (
      <ul className='tabwp'>
        {
            this.props.items.map((item, index) => {
              let isSelected = false
              if (activedType === item.type) {
                isSelected = true
              }
              return <Item onClick={this.clickItem} isSelected={isSelected} key={index} item={item.font} type={item.type} />
            })
        }
      </ul>
    )
  }
}

module.exports = Tab



