import React, { Component } from 'react'
import './index.css'

class BookItem extends Component {
  componentDidMount () {
    console.log('book')
  }
  clickItem (url) {
    console.log(url)
    this.props.onClick(url)
  }
  render () {
    let authors = (this.props.data.author.length && this.props.data.author.join(',')) || ''
    return (
      <div className='bookItem' onClick={this.clickItem.bind(this, this.props.data.url)}>
        <img src={this.props.data.image} alt='图书图片' />
        <div className='descript'>
          <div className='title'>名称：{this.props.data.title}</div>
          <ul className='tagul'>
            {
                this.props.data.tags.map((item, index) => {
                  if (index <= 2) {
                    return <li className='tag' key={index}>{item.title}</li>
                  }
                })
            }
          </ul>
          <div className='authors'>作者：{authors}</div>
          <div className='grade'>评分：{this.props.data.rating.average}</div>
          <div className='time'>时间：{this.props.data.pubdate}</div>
        </div>
      </div>
    )
  }
}

module.exports = BookItem



