import React, { Component } from 'react'
import './index.css'

class MusicItem extends Component {
  render () {
    let authors = []
    this.props.data.author.map(function (item, index) {
      authors.push(item.name)
    })
    let austr = authors.join(',')
    return (
      <div className='musicItem'>
        <img src={this.props.data.image} alt='音乐图片' />
        <div className='descript'>
          <div className='title'>名称：{this.props.data.title}</div>
          <div className='authors'>作者：{austr}</div>
          <div className='grade'>评分：{this.props.data.rating.average}</div>
        </div>
      </div>
    )
  }
}

module.exports = MusicItem



