import React, { Component } from 'react'
import './index.css'
import Cont from './cont'

class TabCont extends Component {
  constructor () {
    super()
    this.showDetail = this.showDetail.bind(this)
  }
  showDetail (url) {
    this.props.onClick(url)
    console.log(url, 'in index')
  }
  render () {
    const activedType = this.props.activedType

    return (
      <ul className='tabcont'>
        {
          this.props.items.map((item, index) => {
            let selectedCls = 'tabli'
            if (activedType === item.type) {
              selectedCls = 'tabli actived'
            }
            return <li className={selectedCls} key={index}>
              <Cont onClick={this.showDetail} type={item.type} isActive={activedType === item.type} />
            </li>
          })
        }
      </ul>
    )
  }
}

module.exports = TabCont



