import React, { Component } from 'react'

import './index.css'

class Search extends Component {
  constructor () {
    super()
    this.state = {
      value: ''
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleKeydown = this.handleKeydown.bind(this)
  }
  handleChange (e) {
    this.setState({ value: e.target.value })
  }
  handleSearch (value) {
    this.props.onSearch(value)
  }
  handleKeydown (e) {
    if (e.keyCode === 13) {
      this.handleSearch(this.state.value)
    }
  }
  render () {
    return (
      <div className='searchwp'>
        <div className='searchcont'>
          <input type='text' onKeyDown={this.handleKeydown} className='searchtext' onChange={this.handleChange} defaultValue={this.state.value} placeholder={this.props.ph} />
          <input type='button' className='searchbtn' value='搜索' onClick={this.handleSearch.bind(this, this.state.value)} />
        </div>

      </div>
    )
  }
}
export default Search



