import React, { Component } from 'react'
import './index.css'
import Search from '../search'
import fetchJsonp from 'fetch-jsonp'
import MusicItem from '../musicItem'
import MovieItem from '../movieItem'
import BookItem from '../bookItem'
class Cont extends Component {
  constructor () {
    super()
    this.state = {
      pt: 70,
      isFrush: false,
      canLose: false,
      isFoot: false,
      isFinish: false,
      data: []
    }
    this.q = ''
    this.tag = ''
    this.start = 0  // offset
    this.count = 10 // count per time
    this.total = 0 // total
    this.getData = this.getData.bind(this)
    this.handleData = this.handleData.bind(this)
    this.handleErr = this.handleErr.bind(this)
    this.searchData = this.searchData.bind(this)
    this.clickItem = this.clickItem.bind(this)
    this.loadData = this.loadData.bind(this)
    this.frushData = this.frushData.bind(this)
  }
  getData (url, param, more) {
    var _self = this
    let p3 = '&start=' + this.start
    let p4 = '&count=' + this.count
    url = url + param + p3 + p4
    fetchJsonp(url)
      .then(function (response) {
        return response.json()
      }).then(function (json) {
        _self.handleData(json, more)
      }).catch(function (ex) {
        _self.handleErr(ex)
      })
  }
  handleData (json, more) {
    let type = this.props.type
    this.total = json.total
    let data = this.state.data.slice(0)
    let lists = []
    if (type === 'book') {
      lists = json.books
    } else if (type === 'music') {
      lists = json.musics
    } else if (type === 'movie') {
      lists = json.subjects
    }
    if (more) {
      data = data.concat(lists)
    } else {
      data = lists
    }
    this.alreadyGet = true
    this.setState({
      isFoot: false,
      data: data
    })
  }

  handleErr (ex) {
    console.log('----err----', ex)
  }

  searchData (value) {
    this.q = value
    if (this.props.isActive) {
      this.getData(this.searchurl, '?q=' + value)
    }
  }
  clickItem (url) {
    this.props.onClick(url)
  }
  componentWillReceiveProps (nextProps) {
    if (nextProps.isActive && !this.alreadyGet) {
      this.getData(this.url, this.param)
    }
  }

  componentDidMount () {
    if (this.props.isActive) {
      this.getData(this.url, this.param)
    }
  }
  handleTouchStart (e) {
    this.startY = (e.touches && e.touches[0] && e.touches[0].clientY) || 0
    this.setState({
      canLose: false,
      isFrush: false,
      pt: 70
    })
  }
  handleTouchEnd (e) {
    const h = 80
    this.setState({
      canLose: false,
      isFrush: false,
      pt: 70
    })
    if (this.moveY - this.startY >= h) {
      this.frushData()
    }
    if (this.moveY < this.startY) {
      this.loadData()
    }
  }
  handleTouchMove (e) {
    const dis = 5
    this.moveY = (e.touches && e.touches[0] && e.touches[0].clientY) || 0
    let scrollTop = document.body.scrollTop || document.documentElement.scrollTop
    let sum = this.moveY - this.startY
    if (this.moveY - this.startY > dis && scrollTop <= 0) {
      e.preventDefault()
      this.setState({
        pt: sum - dis,
        isFrush: true
      })
      if (sum > 80) {
        this.setState({
          canLose: true
        })
      }
    }
  }
  frushData () {
    let scrollTop = document.body.scrollTop || document.documentElement.scrollTop

    if (scrollTop <= 0) {
      console.log('刷新')
      this.start = 0
      this.getData(this.url, this.param)
    }
  }
  loadData () {
    console.log(this.count, this.start, this.total)
    let dataHeight = this.refs.onPullUp.clientHeight
    let scrollTop = document.body.scrollTop || document.documentElement.scrollTop
    let screenHeight = document.documentElement.clientHeight
    const h = 10// 自定义距离底部多少时显示上拉获取更多
    if (dataHeight < screenHeight) {
      this.setState({
        isFoot: false,
        finished: false
      })
    } else {
      this.setState({
        isFoot: true
      })
      if (dataHeight - scrollTop - h < screenHeight) {
        console.log('显示加载更多')
        if (this.count * (this.start + 1) < this.total) {
          this.start ++
          this.getData(this.url, this.param, true)
          this.setState({
            finished: false
          })
        } else {
          this.setState({
            finished: true
          })
        }
      }
    }
  }
  render () {
    let type = this.props.type
    let ph = ''
    let Item = ''
    this.url = ''
    this.param = ''
    if (type === 'book') {
      ph = '书名、作者、ISBN'
      Item = BookItem
      this.searchurl = this.url = 'https://api.douban.com/v2/book/search'
      this.param = '?q=""&tag=""'
    } else if (type === 'music') {
      ph = '唱片名、表演者、条码、ISRC'
      Item = MusicItem
      this.searchurl = this.url = 'https://api.douban.com/v2/music/search'
      this.param = '?q=""'
    } else if (type === 'movie') {
      ph = '电影、影人、影院、电视剧'
      Item = MovieItem
      this.url = 'https://api.douban.com/v2/movie/top250'
      this.searchurl = 'https://api.douban.com/v2/movie/search'
      this.param = '?q=""&tag=""'
    }
    return (
      <div className='tabcc' style={{paddingTop: this.state.pt + 'px'}} >

        { !this.state.isFrush && <Search onSearch={this.searchData} ph={ph} />}
        <div className='tip top'>
          { this.state.isFrush && ((this.state.canLose && <span>松手更新</span>) || <span>下拉刷新</span>)}
        </div>
        <ul className='tablist' ref='onPullUp' onTouchStart={this.handleTouchStart.bind(this)} onTouchMove={this.handleTouchMove.bind(this)} onTouchEnd={this.handleTouchEnd.bind(this)}>
          {
            this.state.data && this.state.data.map((item, index) => {
              return <li key={index}><Item onClick={this.clickItem} data={item} /></li>
            })
          }

        </ul>

        { this.state.finished ? <div className='tip bottom'> <span>我是有底线的</span> </div> : this.state.isFoot && <div className='tip bottom'> <span >上拉加载更多</span> </div>
                    }

      </div>
    )
  }
}

module.exports = Cont



