import React, { Component } from 'react'
import './index.css'

class MovieItem extends Component {
  render () {
    let actors = []
    this.props.data.casts.map((item, index) => {
      actors.push(item.name)
    })
    let actorstr = actors.join(' ')
    return (
      <div className='movieItem'>
        <img src={this.props.data.images.small} alt='电影图片' />
        <div className='descript'>
          <div className='title'>{this.props.data.title} - {this.props.data.year}</div>
          <ul className='tagul'>
            {
              this.props.data.genres.map((item, index) => {
                if (index <= 2) {
                  return <li className='tag' key={index}>{item}</li>
                }
              })
            }
          </ul>
          <div className='actors'>{actorstr}</div>
          <div className='grade'>评分：{this.props.data.rating.average}</div>
        </div>
      </div>
    )
  }
}

module.exports = MovieItem



